/* eslint-disable @next/next/no-img-element */
import LanguageToggle from "../LanguageToggle"

const Header = () => {
    return (
        <div className="w-full bg-white py-3 flex justify-between items-center gap-5">
            <div className="container flex justify-between items-center gap-6">
                <div className="w-28 h-9 overflow-hidden">
                    <Image
                        width={100}
                        height={100}
                        // Using a placeholder image URL for demonstration purposes 
                        src={"https://cdn.10minuteschool.com/images/svg/10mslogo-svg.svg"}
                        alt="10 Minute School Logo"
                    />

                </div>
                <LanguageToggle />
            </div>
        </div>
    )
}

export default Header